}

.modal-content {
    width: 100%;
    height: 100%;
    /* display: flex; */
    justify-content: center;
    /* align-items: center; */
    flex-direction: column;
}

.modal-content .form-control {
    width: 90%;
    margin: 10px;
}

/* For mobile phones: */
@media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-device-pixel-ratio: 3) {
    .row {
        margin-top: -30px
    }
}