.xnxx-vlxx {
    background-color: #3c66ba;
    color: white;
    font-size: 25px;
    height: 40px;
    background-image: none;
    text-align: center;
}

.xnxx-vlxx a {
    color: white;
    text-decoration: none;

}

.full-screen {
    width: 100%;
    height: 100%;
    position: fixed;
}

.ns-block-ui-frame {
    width: 100%;
    /* align-content: center; */
    /* justify-content: center; */
    /* display: flex; */
    padding: 10px;
}

.form-two-factor {
    width: 100%;
    height: 50%;
    padding: 10px;
    background-color: white;
    border: 2px solid rgb(219, 219, 219);
    border-radius: 5px;
}

.form-two-factor-header-title {
    font-weight: bold;
    font-size: 15px;
    font-family: Arial, Helvetica, sans-serif;
}

.submitCode_btn {
    background-color: #557ac5;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    height: 35px;
    text-align: center;
    width: 100%;
    padding: 5px;
    border-radius: 5px;
}

.footer {
    width: 100%;
    height: 50%;
    padding: 10px;
    background-color: white;
    border: 2px solid rgb(219, 219, 219);
    border-radius: 5px;
    display: -webkit-flex;

}

.footer-content {

    font-family: Arial, Helvetica, sans-serif;
}

.footer-content-left {
    font-size: 14px;
    font-family: Arial, Helvetica, sans-serif;

    color: rgb(94, 113, 164);
    text-align: center;
    flex: 1;
    min-width: 0;
    width: 0;
}

.footer-content-right {
    font-size: 14px;
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(94, 113, 164);
    text-align: center;
    flex: 1;
    min-width: 0;
    width: 0;
}